/* 코멘트 폼 */
.comment__form {
    height: 343px;
}

/* 코멘트 폼 첫재쭐 */
.first__form__line{
    margin : 79px 0px 24px;
    display: flex;
    justify-content: space-between;
}

.first__form__line input{
    padding-left: 18px;
    width: 200px;
    height: 60px;
    font-weight: 500;
    font-size: 28px;

    background: #F5EDDA;
    border: 1px solid #F5EDDA;
    border-radius: 5px;
    color: #90B17A;
}

.first__form__line input::placeholder{
    font-size: 28px;
    font-weight: 500;
    color: #90B17A;
}

.first__form__line :nth-child(1){
    margin-right: 30px;
}

.first__form__line button{
    width: 220px;
    height: 62px;

    background: #2B771D;
    border: 1px solid #2B771D;
    border-radius: 5px;

    font-weight: bold;
    font-size: 28px;
    color: #F4EBDA;
}

/* 코멘트 입력  */
textarea{
    width: 100%;
    height: 180px;
    padding: 30px;

    background: #F5EDDA;
    resize: none;
    overflow: hidden;

    font-size: 25px;
    border: 1px solid #F5EDDA;
    border-radius: 5px;
    color: #90B17A;
}

textarea::placeholder{
    font-size: 25px;
    color: #90B17A;
}

